/*Fonts */

@font-face {

  font-family: 'Product Sans';
  src: url(fonts/Product\ Sans\ Bold.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {

  font-family: 'Product Sans';
  src: url(fonts/Product\ Sans\ Regular.ttf);
  font-style: normal;
  font-weight: 200;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: 'Product Sans';
  color: #ffffff;
}

body {
  background-color:#1c1c27;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  background-color:#28293d;
  position: relative;
  width: 500px;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  font-weight: 200;
  position: relative;
  margin-right: 50px;
}

.question-count {
  margin-bottom: 20px;
  font-weight: 300;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  font-weight: 200;
  color: #ffffff;
  background-color: transparent;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #37485f;
  transition: all 0.3s ease-out;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
  border: 3px solid #2f922f;
}

.incorrect {
  background-color: #ff3333;
  border: 3px solid #ff3333;
}

button:hover {
  background-color: #37485f;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}
